.products__wrapper{display:flex;flex:1 1 70%;flex-direction:column;gap:15px}.hold{text-align:center;font-size:15px;line-height:25px}.products{display:flex;flex-wrap:wrap;gap:15px;justify-content:center}.products__item{border:1px solid gray;cursor:pointer;border-radius:10px;padding:10px;color:#000;flex:1 1 230px;display:flex;flex-direction:column;align-items:center;justify-content:center;gap:10px;text-align:center;position:relative;user-select:none}.products__item::after{content:"ОТКРЫТЬ";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.85);border-radius:8px;color:#fff;font-size:23px;display:flex;align-items:center;justify-content:center;opacity:0;transition:all .1s ease 0s}.products__item:hover::after{opacity:1}.products__item:hover .products__add{opacity:1}.products__item:hover .product__loader{opacity:1}.products__img_wrapper{display:flex;overflow:hidden;flex:1 1 auto;align-items:center}.products__img_wrapper img{max-width:100%;max-height:200px}.products__desc{display:flex;align-items:center;gap:9px;font-size:15px;flex:1 1 auto}.products__title{font-weight:600}.products__rate{font-weight:600;font-size:17px}.products__rate_green{color:green}.products__rate_yellow{color:#8a8a31}.products__rate_red{color:red}.products__price{font-size:17px;font-weight:700}.products__add{position:absolute;top:0;left:0;display:flex;align-items:flex-end;justify-content:center;padding:20px 10px;width:100%;height:100%;z-index:10;overflow:hidden;opacity:0}.products__add_text{font-size:20px;color:#fff}.products__add_mark{max-width:60px;max-height:60px;margin-bottom:-5px}.product__loader{opacity:0;position:absolute;top:0;left:0;display:flex;align-items:flex-end;justify-content:center;padding:20px 10px;width:100%;height:100%;z-index:10;overflow:hidden}.product__loader div{margin:0;padding:0}