.myModal {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    background-color: rgba(0, 0, 0, 0.4);
    cursor: pointer;
    border: 1px solid #000;
}

.myModalContent {
    padding: 15px;
    background-color: white;
    border-radius: 16px;
    min-width: 250px;
    cursor: auto;
    width: 90%;
    max-width: 550px;
    @media (min-width: 820px) {
        padding: 25px;
    }
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}
